import RestaurantSource from '../../data/restaurant-source';
import {
    createRestaurantItemTemplate,
} from '../templates/template-creator';

const Home = {
    async render() {
        return `
        <section class="content">
                <h2>List Restorant</h2>
                <div id="restaurant" class="restaurant"></div>
                <h4 class="label-artikel">Artikel Kesehatan</h4>
                 <article-element></article-element>
        </section>
        `;
    },

    async afterRender() {
        const restaurant = await RestaurantSource.listRestaurant();
        const restaurantContainer = document.querySelector('#restaurant');
        restaurant.forEach((restaurants) => {
            restaurantContainer.innerHTML += createRestaurantItemTemplate(restaurants);
        });
    },
};

export default Home;